section.formContainer {
  display: flex;
  margin-top: 64px;
}

form {
  width: 100%;
}

h3.formTitle {
  display: flex;
  flex: 0 0 100%;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
}

section.formBody {
  .col {
    justify-content: space-between;
  }

  .col:nth-child(3) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
  }

  .inputGroup {
    margin-top: 24px;

    .inputLabel {
      font-weight: bold;
      font-size: 12px;
      line-height: 16px;
      color: var(--input-label);
      margin-bottom: 8px;
    }

    input {
      width: 100%;
      padding: 12px 16px;
      font-weight: 400;
      font-size: 12px;
      font-family: 'Noto Sans TC', sans-serif;
      line-height: 16px;
      border: 1px solid var(--input-border);
      border-radius: 4px;
      background-color: transparent;
    }

    input {
      color: var(--input-text);
    }
  }
}