section.formContainer {
  display: flex;
  margin-top: 64px;
}

form {
  width: 100%;
}

h3.formTitle {
  display: flex;
  flex: 0 0 100%;
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
}

section.formBody {


  .col {
    justify-content: space-between;
  }

  .radioGroup {
    display: flex;
    position: relative;
    justify-content: unset;
    padding: 11px 20px;
    margin-top: 24px;
    cursor: pointer;
  
    input[type="radio"] {
      width: 20px;
      height: 20px;
      margin-right: 20px;
      align-self: center;
      -webkit-appearance: none;
    }
  
    input[type="radio"]::before {
      content: "";
      display: block;
      width: inherit;
      height: inherit;
      border: 1px solid #222222;
      border-radius: 50%;
      background-color: #FFFFFF;
    }
  
    input[type="radio"]:checked::before {
      border: 6px solid #2A2A2A;
      background-color: #FFFFFF;
    }
  
    .radioInfo {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      flex-wrap: wrap;
  
      .col {
        margin-bottom: 2px;
      }
      .text {
        font-size: 14px;
        font-weight: 700;
        line-height: 20px;
      }
      .price, .period {
        font-size: 12px;
        line-height: 16px;
      }
    }

    .radioBoxBorder {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
    }

    input[type="radio"] ~ .radioBoxBorder {
      border-radius: 4px;
      border: 1px solid #F0F0F5;
    }

    input[type="radio"]:checked ~ .radioBoxBorder {
      border: 1px solid #2A2A2A;
    }

    #shipping-standard ~ .radioInfo .shipping-text {
      font-weight: normal;
    }
  }
}