section.cartContainer {
  padding: 0px 24px;
  margin-top: 80px;
  border: 1px solid #F0F0F5;
  border-radius: 8px;

  h3.cartTitle {
      display: flex;
      flex: 0 0 100%;
      margin: 32px 0px;
      font-weight: 700;
      font-size: 18px;
      line-height: 24px;
  }
}

section.productList {
  .productContainer {
      display: flex;
      flex-wrap: nowrap;
      margin-top: 2px;
      margin-bottom: 32px;

      .imgContainer {
          width: 100px;
          height: 100px;
          margin-right: 21px;
      }
      .productInfo {
          display: flex;
          flex-wrap: wrap;
          flex-grow: 1;
          align-content: flex-start;

          .productName {
              font-size: 16px;
              line-height: 24px;
              font-weight: 400;
              max-width: 60%;
              margin-right: auto;
          }

          .productControlContainer {
              display: block;
              width: 100%;
              order: 1;
          }

          .productControl {
              display: flex;
              width: 116px;
              margin-top: 18px;
              margin-bottom: 17px;
              justify-content: space-between;
              align-items: center;

              .productAction {
                  display: inline-block;
                  width: 27px;
                  height: 26px;
                  cursor: pointer;
              }

              .productCount {
                  display: inline-block;
              }
          }

          .price {
              font-size: 16px;
              line-height: 22px;
              font-weight: 700;
          }
      }
  }
}

section.cartInfo {
  border-top: 1px solid #F0F0F5;
  padding-top: 16px;
  padding-bottom: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .text {
      font-weight: normal;
      font-size: 14px;
      line-height: 20px;
      color: var(--main-text);
  }

  .price {
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      color: #000000;
  }
}