@import "../../App.scss";

footer.siteFooter {
  display: block;
  position: relative;
  bottom: 0px;
  margin-top: 120px;
  padding-left: var(--site-edge-margin);
  padding-right: var(--site-edge-margin);
  background-color: var(--footer-background-color);

  .footerContainer {
      display: flex;
      position: relative;
      max-width: 1100px;
      margin: 0 auto;
      flex-wrap: nowrap;
      justify-content: space-between;

      .footerLogoContainer {
        display: flex;
        margin: auto 0;
        height: 32px;
        width: 172px;

          .cursorPoint {
              height: 100%;
              width: 100%;
          }
      }
  }


  .footerSection {
      display: flex;
      flex-direction: column;
      margin-top: 50px;
      margin-bottom: 24px;

      .sectionTitle {
          font-size: 24px;
          font-weight: bold;
          line-height: 24px;
          color: var(--main-text);
      }

      .sectionContent {
          display: flex;
          flex-direction: column;
          margin-top: 24px;
          color: var(--secondary-text);
          & > * {
              color: inherit;
          }

          .pageLink {
              margin-bottom: 8px;
              font-weight: normal;
              line-height: 17px;
              text-decoration: none;
              cursor: pointer;
          }
          .telInfo {
              margin-bottom: 16px;
              font-family: 'Nunito Sans', sans-serif;
              font-weight: 600;
              line-height: 16px;
          }
          .socialIcon {
              width: 24px;
              height: 24px;
              margin-right: 16px;
          }
      }
  }
}

@media screen and (max-width: $screen-break-point) {
  footer.site-footer {
      display: none;
  }
}